import { HashRouter, Route, Routes, unstable_HistoryRouter as HistoryRouter } from 'react-router-dom';
import DeviceOrientation, { Orientation } from 'react-screen-orientation';

import { createBrowserHistory } from "history";

import './App.css';
import MainScreen from './pages/main/MainScreen';
import RotateNotice from './pages/rotateNotice/RotateNotice';

function App() {

  const history = createBrowserHistory({ window });

  return (
    <DeviceOrientation lockOrientation={'landscape'}>
      <Orientation orientation='landscape' alwaysRender={false} >
        <RotateNotice />
      </Orientation>
      <Orientation orientation='portrait' alwaysRender={false}>
        <div className="App">
          <HistoryRouter history={history}>
            <Routes>
              <Route exact path='/' element={<></>} />
              <Route exact path='/maxnastya' element={<MainScreen date={'2022-09-05 18:00:00'} />} />
              <Route exact path='/slavanastya' element={<MainScreen date={'2022-06-06 17:40:00'} />} />
              <Route exact path='/vovadasha' element={<MainScreen date={'2019-06-02 23:15:00'} />} />
            </Routes>
          </HistoryRouter>
        </div>
      </Orientation>
    </DeviceOrientation>
  );
}

export default App;
